@import "./particles.scss";
@import "./animate.scss";
@import "./bootstrap.scss";
@import "./fontAwesomeScss/fontawesome.scss";
@import "./fontAwesomeScss/solid.scss";
@import "./fontAwesomeScss/brands.scss";
//@import "main.min.css";
$color-dollarGreen: #184A45;
$color-white:rgb(255, 255, 255);
$color-main:#d2be30;
$color-SecondMain: #184A45;
$color-secondGrad: #366721;
$color-yellow:#d2be30;
$color-yellowLush:rgb(251, 220, 24);
$color-a-yellowLush:rgba(251, 220, 24, 0.2);
$color-a-green:rgba(113, 223, 66, 0.2);
$color-disabled: #d1d1d1;
//#ed1c94

body {
    margin: 0;
    padding: 0;
    line-height: 1.85em;
    font-family: 'Poppins', sans-serif;
    color: #242732;
}

h1, h2, h3, h4, h5, h6 {
    //font-family: 'Raleway',sans-serif;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

img {
    border: none;
    margin: 0;
    padding: 0;
}

a {
    margin: 0;
    padding: 0;
    cursor: pointer;
    text-decoration: none;
    -o-transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
    -ms-transition: all .3s;
    color: $color-main;
}

a:hover {
    text-decoration: none;
}

input, a:focus {
    outline: 0;
}

.btn:focus {
    box-shadow: none
}

p {
    padding-bottom: 10px;
    line-height: 26px;
}

h2 {
    font-size: 40px;
    line-height: 42px;
    text-align: center;
    font-weight: 900;
}

.overflow {
    overflow: hidden;
}

.block-center {
    margin: 0 auto;
}

/* Preloader */
.preloader {
    background-color: #fff;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10000;
}

.preloader .loader {
    width: 60px;
    height: 60px;
    line-height: 80px;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
}

.preloader .loader:before {
    content: "";
    width: 100%;
    height: 100%;
    border-top: 2px solid $color-main;
    border-right: 2px solid #2575fc;
    border-bottom: 2px solid #2575fc;
    border-left: 2px solid #2575fc;
    position: absolute;
    left: 0;
    top: 0;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    -ms-border-radius: 50%;
    border-radius: 50%;
    -webkit-animation: spin 1s linear infinite;
    -moz-animation: spin 1s linear infinite;
    -o-animation: spin 1s linear infinite;
    -ms-animation: spin 1s linear infinite;
    animation: spin 1s linear infinite;
}

.preloader i:before {
    font-size: 35px;
}

/* Preloader animation */
@-webkit-keyframes spin {
    form {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes spin {
    form {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

/* Spacer */
.spacer_30vh {
    height: 30px
}

.spacer_20vh {
    height: 20px
}

.spacer_10vh {
    height: 10px
}

/* Button */
.btn-ntPrimaryFull {
    min-width: 120px;
    background-color: $color-main;
    border: 2px solid $color-main;
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
    letter-spacing: 1px;
    border-radius: 50px;
    outline: none;
    display: inline-block;
    margin-right: 10px;
}

.btn-ntPrimaryFull:hover {
    border: 2px solid $color-main;
    background-color: transparent;
    color: $color-main;
}

.btn-ntPrimaryBorder {
    min-width: 120px;
    background-color: transparent;
    border: 2px solid $color-main;
    text-transform: uppercase;
    font-weight: 700;
    color: $color-main;
    letter-spacing: 1px;
    border-radius: 50px;
    outline: none;
    display: inline-block;
    margin-right: 10px;
}

.btn-ntPrimaryBorder:hover {
    border: 2px solid $color-main;
    background-color: $color-main;
    color: #ffffff;
}

.btn.ntLargexx {
    font-size: 16px;
    padding: 16px 29px;
}

.btn.ntLarge {
    font-size: 14px;
    padding: 14px 27px;
}

.btn.ntMedium {
    font-size: 12px;
    padding: 12px 25px;
}

.btn.ntSmall {
    font-size: 10px;
    padding: 10px 23px;
}

/* Way Point */
.ntWayPoint {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(100% - 2px);
    display: flex;
    cursor: pointer;
}

.ntWayPoint::after {
    content: '';
    background-image: url('../images/way-point.svg');
    width: 253px;
    height: 38px;
}

.ntWayPoint i {
    text-align: center;
    position: absolute;
    top: 15px;
    left: 0;
    right: 0;
    color: #fff;
    font-size: 25px;
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55) all;
    animation: bounce_10px 2s ease infinite;
}

.ntWayPoint:hover i {
    top: 15px;
}

/* Section Heading */

span.btm-bar {
    background: url('../images/h1-btm-bar.png') no-repeat;
    width: 130px;
    height: 17px;
    display: block;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 15px;
    margin-top: 15px;
}

span.btm-bar-white {
    background: url('../images/bar-white.png') no-repeat;
    width: 130px;
    height: 17px;
    display: block;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 15px;
    margin-top: 15px;
}

/**
 * 2.0 Header (Logo or navigation)
 * -----------------------------------------------------------------------------
 */

/*Logo*/
#logo_home {
    position: relative;
    z-index: 3;
}

#logo_home {
    margin-bottom: 0;
    position: relative;
}

#logo_home a {
    width: 180px;
    height: 90px;
    display: block;
    //background-image: url('../images/cashLogo.png');
    background-repeat: no-repeat;
    background-position: left top;
    text-indent: -9999px;
    position: relative;
}
#logo_home img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
}

/*Navigation*/
.ntNavbar {
    z-index: 3;
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: absolute;
    right: 0;
}

.ntNavbar li {
    position: relative
}

.ntNavbar li a {
    color: #fff;
    text-decoration: none;
    font-weight: 500;
    font-size: 15px;
    margin: 0 15px;
    display: block;
    position: relative;
}

.ntNavbar li a.active,
.ntNavbar li a:hover {
    color: $color-main;
    text-shadow: none;
}

.ntNavbar li a:before {
    content: "";
    position: absolute;
    background-color: $color-main;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all .3s ease-in-out 0s;
    transition: all .3s ease-in-out 0s;
    height: 2px;
    bottom: -5px;
    opacity: 1;
    left: 0;
    width: 100%;
}

.ntNavbar li a:hover:before,
.ntNavbar li a.active:before,
.ntNavbar li a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

/*Sticky Header*/
.ntHeader.fixed {
    z-index: 4;
    position: fixed;
    border-bottom: 0;
    padding: 10px 0;
    transition: 450ms ease-out;
}

.ntHeader.fixed.slideDown {
    top: 0;
    background-color: #fcfcfc;
    box-shadow: 0 0 2px rgba(50, 50, 50, 0.4);
}

.fixed .ntNavbar li a {
    color: #242732;
}

.fixed #logo_home a {
    //background-image: url('../images/logo-color-fixed.png');
    width: 110px;
    height: 33px;
}
.fixed #logo_home img {
    width: 70%;
    height: auto;
}

.ntInnerSection {
    padding-top: 80px;
    padding-left: 0px;
    padding-right: 0px;
    position: relative;
}

/*Mobile Nav*/
.navbar {
    padding: 0;
}

.navbar-toggler {
    position: relative;
    z-index: 9;
    right: 0;
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 4px 6px;
}

.navbar-toggler-icon {
    width: 25px;
    height: 25px;
    background-image: url("data:image/svg+xml;charset=utf8,<svg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'><path stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/></svg>");
    
}

.navbar-toggler:focus, .navbar-toggler:hover {
    text-decoration: none;
    text-shadow: none;
    outline: 0;
}

/**
 * 3.0 Home (Section)
 * -----------------------------------------------------------------------------
 */

.ntHome {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-image: url('../images/header-area.jpg');
    background-size: cover;
    background-position: center;
    border-bottom: 4px solid #fff;
    min-height: 570px;
}

.ntHome.particles{
    background-image: url('../images/header-area-01.jpg');
}

.ntHome:before {
    content: '';
    z-index: 1;
    width: 100%;
    height: 100%;
    position: absolute;
    background-repeat: no-repeat;
    bottom: 0;
    right: 0;
    top: 0;
    background-image: linear-gradient(to right, $color-secondGrad 0%, #2575fc 100%);
    opacity: 0.6;
}

.ntHeader {
    width: 100%;
    padding: 20px 0;
    z-index: 4;
    position: absolute;
    top: 0;
}

.ntHome .ntWayPoint {
    bottom: -2px;
    z-index: 2;
}

.ntHome .ntWayPoint::after {
    content: '';
    background-image: url('../images/way-point-white.svg');
    width: 253px;
    height: 38px;
}

.ntHome .ntWayPoint i {
    text-align: center;
    color: $color-main;
    top: 15px;
}

span.line {
    border-top: 2px solid $color-main;
    display: block;
    margin-top: 15px;
}

