}

.mainTitle .location {
    overflow: hidden;
    display: block;
    margin-bottom: 25px;
}

.mainTitle span.where,
.mainTitle span.when {
    float: left;
    display: block;
    width: 50%;
    font-weight: 400;
    text-shadow: 1px 1px 0px #0000007a;
    font-size: 18px;
}

.mainTitle .location span i {
    background: $color-yellow;
    padding: 15px 20px;
    margin-right: 15px;
    display: block;
    float: left;
    font-size: 25px;
    text-align: center;
    border-radius: 3px;
}

.mainTitle .location span b {
    display: block;
    text-transform: uppercase;
    color: $color-main;
    font-weight: 700;
    text-shadow: none;
    font-size: 14px;
    line-height: 16px;
    margin-top: 0px;
}

.mainTitle h1 {
    color: #ffffff;
    font-weight: 600;
    font-size: 45px;
    padding-top: 5px;
    padding-bottom: 5px;
    line-height: 50px;
    text-shadow: 1px 1px 0px #0000001a;
    text-align: left;
}

.mainTitle p {
    font-weight: 300;
    margin-top: 15px;
    font-size: 16px;
    line-height: 24px;
    text-shadow: 1px 1px 0px #0000001a;
}

.mainTitle h1 b {
    color: #ffffff;
}

.mainTitle .btn-ntPrimaryBorder {
    color: #ffffff;
}

.mainTitle .btn-ntPrimaryBorder:hover {
    border-color: $color-main;
}

.mainTitle .btn-ntPrimaryFull:hover {
    color: #ffffff;
}

@media screen and (max-height: 768px) {
    .mainTitle {
        bottom: 15%;
    }
}

.cd-words-wrapper {
    display: inline-block;
    position: relative;
    text-align: left;
}

.cd-words-wrapper b {
    display: inline-block;
    position: absolute;
    white-space: nowrap;
    left: 0;
    top: 0;
}

.cd-words-wrapper b.is-visible {
    position: relative;
}

.no-js .cd-words-wrapper b {
    opacity: 0;
}

.no-js .cd-words-wrapper b.is-visible {
    opacity: 1;
}

/* Text Clip Animation */
.cd-headline.clip span {
    display: inline-block;
    padding: .2em 0;
}

.cd-headline.clip .cd-words-wrapper {
    overflow: hidden;
    vertical-align: top;
    font-size: 75px;
    display: block;
}

.cd-headline.clip .cd-words-wrapper::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background-color: #aebcb9;
}

.cd-headline.clip b {
    opacity: 0;
}

.cd-headline.clip b.is-visible {
    opacity: 1;
}

