    font-weight: 400;
}

.ntSpeaker .ntSubTitle {
    margin-bottom: 60px;
    text-align: center;
}

/* Speakers Items */
.ntOurSpeakersItem {
    position: relative;
    overflow: hidden;
    margin-bottom: 20px;
    transition: 0.5s;
    -webkit-transition: 0.5s;
}

.ntOurSpeakersItem::after,
.ntOurSpeakersItem::before {
    width: 95.5555%;
    height: 93.5555%;
    margin: 2.6666%;
}

.ntOurSpeakersItem:hover::after,
.ntOurSpeakersItem:hover::before {
    -webkit-transform: scale(0);
}

.ntOurSpeakersItem img {
    width: 100%;
    display: block;
}

.ntOurSpeakersItem .caption {
    position: absolute;
    bottom: 0;
    background: #ffffffbd;
    width: 100%;
    padding: 25px 20px;
}

.ntOurSpeakersItem .ntSuperTitle {
    text-transform: uppercase;
    font-size: 10px;
    color: $color-secondGrad;
    margin-bottom: 2px;
    letter-spacing: 1.5px;
    text-align: left;
    line-height: 10px;
    font-weight: 600;
}

.ntOurSpeakersItem .ntSubTitle {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    margin-bottom: 0;
}

.ntOurSpeakersItem h3 {
    font-weight: bold;
    text-transform: capitalize;
    display: inline-block;
    font-size: 20px;
    margin-bottom: 0;
}

.ntOurSpeakersItem h3::after {
    content: '';
    clear: both;
    width: 100%;
    display: block;
    border-bottom: 2px solid $color-main;
    margin-top: 3px;
}

.ntOurSpeakersItem .overlay {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, $color-a-green 0%, $color-a-yellowLush 100%);
    padding: 30px;
    display: none;
    color: #ffffff;
    padding-top: 40%;
}

