}

.ntPackagesDetails {
    background: #fff;
    border-radius: 0px 0px 80px 0px;
}

.ntPackagesDetails ul {
    margin: 0;
    list-style-type: none;
    padding: 0 0 20px 0;
}

.ntPackagesDetails ul li {
    border-bottom: 2px dotted #dedede;
