    }
}

/* Laptop */
@media screen and (max-width: 1024px) {
    .cd-nav-trigger {
        display: block;
    }
}

/* Tablet (landscape) */
@media screen and (max-width: 991px) {
    /*Main Title*/
    .mainTitle {
        width: 70%;
    }

    .mainTitle h1 {
        font-size: 35px;
        line-height: 32px;
    }

    .cd-headline.clip .cd-words-wrapper {
        font-size: 55px;
    }

    .mainTitle span.where, .mainTitle span.when {
        margin-bottom: 15px;
        font-size: 16px;
    }

    .mainTitle .location span i {
        padding: 12px 15px;
        margin-right: 10px;
        font-size: 20px;
    }

    /*Mobile Nav*/
    .ntNavbar {
        left: 0;
        background: #fcfcfc;
        padding: 10px;
        margin-top: 20px;
        position: relative;
    }

    .ntNavbar li a {
        line-height: 45px;
        border-bottom: 1px solid #ddd;
        color: #333;
    }

    .ntNavbar li:last-child a {
        border-bottom: 0;
    }

    .ntNavbar li a:before {
        display: none;
    }

    .fixed .navbar-toggler-icon {
        background-image: url("data:image/svg+xml;charset=utf8,<svg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'><path stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/></svg>");
    }

    .fixed .navbar-toggler {
        border: 1px solid rgba(0, 0, 0, 0.5);
    }

    .fixed .ntNavbar li a,
    .fixed .ntNavbar {
        padding-left: 0;
        padding-right: 0;
        margin: 0;
    }

    /*Button*/
    .btn-ntPrimaryBorder,
    .btn-ntPrimaryFull {
        margin-right: 0;
    }

    .btn.ntMedium {
        font-size: 11px;
        padding: 12px 18px;
    }

    /* Statistic */
    .ntCounter_type span.ntCounterTitle {
        margin-bottom: 80px;
    }

    .ntCounter_type .col-md-3:last-child span.ntCounterTitle {
        margin-bottom: 0px;
    }

    .ntCounter_type i {
        left: 25%;
    }

    /* speaker */
    .ntSubTitle {
        margin-bottom: 36px;
    }

    .ntOurSpeakersItem .overlay {
        padding-top: 20%;
    }

    /* conference schedule */
    .ntTabContent .ntEvent.extend .ntCaption .ntImage {
        float: initial;
        margin: 0 auto;
        margin-bottom: 20px;
    }

    .ntTabContent .ntEvent.extend .ntCaptionText {
        text-align: center;
    }

    /* subscribe */
    .ntSubscribe .form {
        width: 80%;
    }

    .ntSubscribe:after,
    .ntSubscribe:before {
        width: 10%;
    }

}

/* Tablet (portrait) */
@media screen and (max-width: 767px) {
    /* community */
    .ntCommunity ul.ntSocialIcons {
        width: 83%;
    }

    .ntCommunity ul.ntSocialIcons a {
        width: 54px;
        height: 54px;
        font-size: 22px;
        line-height: 54px;
        margin-bottom: 10px;
    }

    .ntOurSpeakersItem .overlay {
        padding-top: 40%;
    }

    /* subscribe */
    .ntSubscribe .form {
        width: 100%;
        padding: 0 12%;
    }

    .ntSubscribe,
    .ntSubscribeLeft,
    .ntSubscribeRight,
    .ntSubscribe:before,
    .ntSubscribe:after {
        display: none
    }

    ul.ntScheduleTab li .ntBtnDownloadSchedule {
        display: none;
    }
    ul.ntScheduleTab{
        width: 280px;
        margin: 0 auto;
    }

    .ntSponsors .ntSponsorList img{
        width: inherit;
    }

    /*footer*/
    footer .footnav,
    footer p {
        margin-bottom: 25px;
        padding-bottom: 0;
    }

    footer .foot-logo {
        margin-top: 0px;
    }
}

/*Mobile*/
@media screen and (max-width: 480px) {

    /* main title */
    .mainTitle {
        width: 90%;
