    .ntContactDetails {
        margin-right: 0;
        position: relative;
        right: 0;
        top: 0;
        width: 100%;
        background: #f7f9fb;
    }

    .ntCommunity ul.ntSocialIcons {
        width: 94%;
    }

    .ntCommunity ul.ntSocialIcons a {
        width: 54px;
        height: 54px;
        font-size: 22px;
        line-height: 54px;
        margin-bottom: 10px;
    }

    .ntRegisterForm .ntSubmit {
        width: 100%;
    }

    /*Register*/
    .ntRegister h3 > b {
        font-size: 23px;
    }

    .ntRegister h3 {
        font-size: 17px;
        line-height: 28px;
    }

    .ntSubTitle {
