        padding: 5px 15px;
    }

    /*Conference Schedule*/
    .ntTabContent .ntEvent .tnEventInfo {
        width: 95%;
        margin-left: 15px;
        padding-left: 20px;
    }

    .ntTabContent .ntEvent .ntEventTime {
        width: 95%;
        text-align: left;
        padding-right: 0;
        padding-left: 35px;
        font-size: 15px;
    }

    .ntTabContent .ntEvent .ntEventTime span {
        top: 10px;
        left: 11px;
    }

    .ntTabContent .ntEvent.extend span {
        left: 0;
        right: 0;
        top: 2px;
        width: 28px;
        height: 28px;
    }

}

/* Mini-Mobile */
@media screen and (max-width: 320px) {

